import { useEffect, useState } from 'react';
import { getInfo } from '../helpers/getInfo';

export const useFetch = () => {

  const [stateInfo, setstateInfo] = useState({
    info: [],
    loading: true
  });

  	useEffect(() => {
      getInfo()
        .then( info => {
          setstateInfo({
            info: info,
            loading: false
          });
        })
	}, []);

  return stateInfo;
}