import React from "react";

export const SideImages = ({images = []}) => {

	return (
		<ul className="container-list">
			{images.map((i) => {
				return (
					<li className="container-image-side">
						<img className="image" src={i} alt="" key={i} />
					</li>
				);
			})}
		</ul>
	);
};


